<script type="text/javascript">
export default {
    props: {
        value: {
            type: [String, Number, Boolean, Object, Array],
            default: ''
        },
        title: {
            type: [String, Number, Boolean],
            default: ''
        },
        placeholder: {
            type: [String, Number, Boolean],
            default: ''
        },
        max: {
            type: [String, Number, Boolean, Object, Array],
            default: 8
        },
        disabled: {
            type: [String, Number, Boolean, Object, Array],
            default: false
        },
    },
    data() {
        return {
            dialogVisible: false,
            inputData: ""
        };
    },
    /**
     * 
     * @type {Object}
     */
    computed: {

    },
    /**
     * 
     * @return {[type]} [description]
     */
    created() {

    },
    /**
     * 
     * @type {Object}
     */
    watch: {

    },
    /**
     * 
     * @return {[type]} [description]
     */
    async mounted() {

    },
    methods: {
        /**
         * 
         * @return {[type]} [description]
         */
        handleOpen() {
            if (!this.disabled) {
                this.dialogVisible = true;
            }

        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleClose() {
            this.dialogVisible = false;
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleConfirm() {
            this.$emit("confirm", this.inputData);
            this.handleClose();
        },
        /**
         * 
         * @return {[type]} [description]
         */
        handleClosed() {
            this.inputData = "";
        }
    },
    render(h) {
        let { dialogVisible, handleOpen, handleClose, handleConfirm, placeholder, title } = this;
        //DOMupload
        const trigger = this.$slots.trigger || this.$slots.default;

        //
        const defaultData = {
            on: {
                click: handleOpen
            }
        };

        //
        const dialogData = {
            props: {
                "visible": dialogVisible,
                "top": "20%",
                "title": title,
                "width": "360px",
                "append-to-body": true,
                "destroy-on-close": true,
                "before-close": handleClose
            },
            on: {
                "closed": this.handleClosed
            }
        };
        //
        return <div class="dialog" {...defaultData}>
            {trigger}
            <el-dialog {...dialogData}>
                <el-input v-model={this.inputData} placeholder={placeholder} maxlength={this.max} show-word-limit ></el-input>
                <span slot="footer" class="dialog-footer">
                    <el-button on-click={handleClose}> </el-button>
                    <el-button type="primary" on-click={handleConfirm} on-close={handleClose}> </el-button>
                </span>
            </el-dialog>
        </div>;
    }
};
</script>
<style lang="less" scoped>
@import '../photoSelect/style.less';
</style>