<template>
    <div class="le-matter">
        <div class="he-title">
            <el-breadcrumb separator-class="el-icon-arrow-right" class="he-link-text">
                <el-breadcrumb-item class="he-link">
                    <he-link href="channel/index">
                        
                    </he-link>
                </el-breadcrumb-item>
                <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <template v-if="platform === 'wechat'">
                <div class="he-header">
                    <div>
                        <span class="he-title-1"> </span>
                        <span class="he-title-2">></span>
                    </div>
                    <div class="he-title-3">/</div>
                </div>
                <div class="he-body">
                    <div class="he-title-4"></div>
                    <el-table :data="tableData" style="width: 760px" :row-class-name="tableRowClassName">
                        <el-table-column prop="name" width="180">
                        </el-table-column>
                        <el-table-column label="" width="180">
                            <template slot-scope="scope">
                                <he-icon v-if="scope.row.regularSubscription" class="he-icon" type="le-icon-tick" size="10px"></he-icon>
                            </template>
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <he-icon v-if="scope.row.certifiedSubscription" class="he-icon" type="le-icon-tick" size="10px"></he-icon>
                            </template>
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <he-icon v-if="scope.row.generalService" class="he-icon" type="le-icon-tick" size="10px"></he-icon>
                            </template>
                        </el-table-column>
                        <el-table-column label="">
                            <template slot-scope="scope">
                                <he-icon v-if="scope.row.certificationServices" class="he-icon" type="le-icon-tick" size="10px"></he-icon>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
                <div class="he-footer">
<!--                    <el-button type="primary"></el-button>-->
                    <he-link href="channel/manualBinding" :query="{platform: 'wechat'}">
                        <el-button type="primary" plain></el-button>
                    </he-link>
                </div>
            </template>
            <template v-if="platform === 'applets'">
                <div class="he-applets_header flex align-center">
                    <span class="he-applets__sign"></span>
                    <span class="he-applets__title"></span>
                </div>
                <el-form label-width="180px" :rules="rules" :model="form" ref="bind">
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="AppID" prop="AppID">
                        <el-input v-model="form.AppID" placeholder="AppID"></el-input>
                    </el-form-item>
                    <el-form-item label="AppSecret" prop="AppSecret">
                        <el-input v-model="form.AppSecret" placeholder="AppSecret"></el-input>
                    </el-form-item>
                    <el-form-item label="ID">
                        <el-input v-model="form.oldID" placeholder="ID"></el-input>
                    </el-form-item>
                </el-form>
            </template>
            <div class="le-cardpin">
                <el-button type="primary" @click="save('bind')"></el-button>
            </div>
        </el-card>
    </div>
</template>
<script>
export default {
    name: "bind",
    data() {
        return {
            title: '',
            tableData: [
                {
                    name: "",
                    regularSubscription: true,
                    certifiedSubscription: true,
                    generalService: true,
                    certificationServices: true
                },
                {
                    name: "",
                    regularSubscription: false,
                    certifiedSubscription: true,
                    generalService: true,
                    certificationServices: true
                },
                {
                    name: "/",
                    regularSubscription: false,
                    certifiedSubscription: true,
                    generalService: false,
                    certificationServices: true
                },
                {
                    name: "",
                    regularSubscription: false,
                    certifiedSubscription: false,
                    generalService: false,
                    certificationServices: true
                },
                {
                    name: "",
                    regularSubscription: false,
                    certifiedSubscription: false,
                    generalService: false,
                    certificationServices: true
                },
                {
                    name: "",
                    regularSubscription: false,
                    certifiedSubscription: false,
                    generalService: false,
                    certificationServices: true
                }
            ],
            platform: '',
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                AppID: [
                    { required: true, message: 'AppID', trigger: 'blur' },
                ],
                AppSecret: [
                    { required: true, message: 'AppSecret', trigger: 'blur' },
                ]
            },
            form: {
                name: '',
                AppID: '',
                AppSecret: '',
                oldID: ''
            }
        }
    },
    mounted() {
        let platform = this.$getQueryVariable('platform');
        this.platform = platform;
        switch (platform) {
            case "applets":
                this.title = "";
                break;
            case "wechat":
                this.title = "";
                break;
        }

        this.getConfig();
    },
    methods: {
        getConfig: function() {
            this.$heshop.appconfig('get').then(res => {
                this.form = res.apply.weapp
            }).catch(err => {
                this.$message.error(err.data.message);
            });
        },
        save: function(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$heshop.appconfig('post', {
                            behavior: 'save',
                            key: 'apply_weapp'
                        },
                        this.form
                    ).then(res => {
                        this.$notify({
                            title: '',
                            message: '',
                            type: 'success'
                        });
                    }).catch(err => {
                        console.error(err)
                        this.$message.error(err.data.message);
                    });
                }
            });
        },
        tableRowClassName({ row, rowIndex }) {
            if (rowIndex === 1) {
                return 'warning-row';
            } else if (rowIndex === 3) {
                return 'warning-row';
            } else if (rowIndex === 5) {
                return 'warning-row';
            }
            return '';
        }
    }
};
</script>
<style scoped>
.he-title {
    margin-bottom: 24px;
}

/deep/ .el-card__body {
    padding: 32px;
}

.he-header {
    border-bottom: 1px solid #DCDFE6;
}

.he-title-1 {
    font-size: 16px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #000000;
    opacity: 0.85;
}

.he-title-2 {
    font-size: 16px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #623CEB;
    margin-left: 20px;
    cursor: pointer;
}

.he-title-3 {
    font-size: 14px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #000000;
    opacity: 0.65;
    margin-bottom: 23px;
    margin-top: 10px;
}

.he-title-4 {
    font-size: 14px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #000000;
    opacity: 0.45;
    margin-top: 24px;
    margin-bottom: 19px;
}

/deep/.warning-row {
    background: #F7F8FA;
}

/deep/.el-table tr {
    height: 44px;
}

/deep/ .el-table thead th {
    background-color: rgba(59, 180, 48, 1) !important;
    color: #FFFFFF;
}

.he-footer {
    margin-top: 20px;
}

.he-icon {
    color: rgba(59, 180, 48, 1);
}

.he-applets_header {
    margin-bottom: 20px;
}

.he-applets__title {
    font-size: 16px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #000000;
    opacity: 0.85;
    margin-left: 12px;
}

.he-applets__sign {
    display: inline-block;
    width: 4px;
    height: 16px;
    background: #623CEB;
}

.flex {
    display: flex;
}

.align-center {
    align-items: center;
}
</style>
