                <div class="control-item-title"></div>
                <el-radio-group v-model="content.type">
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="2"></el-radio>
                </el-radio-group>
                <div class="empty"></div>
                <goodsSelet v-if="content.type==1" v-model="content.goods" :number="50" :limit="50-content.goods.length" @confirm="handleGoodsConfirm"></goodsSelet>
                <classifySelect v-if="content.type==2" v-model="content.group"></classifySelect>
                <el-divider></el-divider>
                <div class="control-item-title"></div>
                <el-form-item label="">
                    <convert v-model="content.is_title" on="" off=""></convert>
                </el-form-item>
                <el-form-item label="">
                    <convert v-model="content.is_price" on="" off=""></convert>
                </el-form-item>
                <el-form-item label="">
                    <convert v-model="content.is_button" on="" off=""></convert>
