export default {
  namespaced: true,
  state: {
    groupInfo: {
      id: -1,
      name: ''
    },
    groupList: [],
    photoList: [],
    pageInfo: {
      size: 15,
      total: 0,
      current: 1
    }
  },
  mutations: {
    /**
     * 
     * @param {[type]} state [description]
     * @param {[type]} data  [description]
     */
    setGroupInfo(state, data) {
      state.groupInfo = data;
    },
    /**
     * 
     * @param {[type]} state [description]
     * @param {[type]} data  [description]
     */
    setGroupList(state, data) {
      state.groupList = data;
    },
    /**
     * 
     * @param {[type]} state [description]
     * @param {[type]} data  [description]
     */
    setPageInfo(state, data) {
      state.pageInfo = data;
    },
    /**
     * 
     * @param {[type]} state [description]
     * @param {[type]} data  [description]
     */
    setPhotoList(state, data) {
      state.photoList = data;
    }
  },
  actions: {
    upadteGroupInfo({ state, commit, dispatch }, value) {
      commit('setGroupInfo', value);
    },
    /**
     * 
     * @param  {[type]} options.commit   [description]
     * @param  {[type]} options.userName [description]
     * @param  {[type]} options.password [description]
     * @return {[type]}                  [description]
     */
    LoadGroupList({ state, commit, dispatch }, id = 0) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .group('get', { include: 'gallery', parent_id: id, type: 2 })
          .then(data => {
            commit('setGroupList', data);
            resolve(data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.commit   [description]
     * @param  {[type]} options.userName [description]
     * @param  {[type]} options.password [description]
     * @return {[type]}                  [description]
     */
    InterGroupItem({ state, commit, dispatch }, { name, id }) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .group(
            'post',
            { include: 'gallery' },
            {
              name: name,
              type: 2,
              parent_id: id || 0,
              sort: 5
            }
          )
          .then(data => {
            VM.$message({
              type: 'success',
              message: ''
            });
            resolve(data);
          })
          .catch(error => {
            if (error.data.message) {
              VM.$message({
                type: 'error',
                message: error.data.message
              });
            } else {
              VM.$message({
                type: 'error',
                message: ''
              });
            }
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.commit   [description]
     * @param  {[type]} options.userName [description]
     * @param  {[type]} options.password [description]
     * @return {[type]}                  [description]
     */
    DeleteGroupItem({ state, commit, dispatch }, { id, type }) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .group('delete', id, { include: 'gallery' })
          .then(data => {
            VM.$message({
              type: 'success',
              message: ''
            });
            resolve(data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.commit   [description]
     * @param  {[type]} options.userName [description]
     * @param  {[type]} options.password [description]
     * @return {[type]}                  [description]
     */
    UpdateGroupItem({ state, commit, dispatch }, value) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .group('put', { include: 'gallery', id: parseInt(value.id) }, { name: value.name })
          .then(data => {
            VM.$message({
              type: 'success',
              message: ''
            });
            resolve(data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.state  [description]
     * @param  {[type]} options.commit [description]
     * @return {[type]}                [description]
     */
    LoadPhotoList({ state, commit }, { id, current, pageSize }) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      let $pageSize = pageSize || 24;
      let $id = id || 0;
      return new Promise((resolve, reject) => {
        $heshop
          .gallery('get', { group_id: parseInt($id), type: 2 })
          .page(current, $pageSize)
          .then(data => {
            let size = parseInt(data.headers['x-pagination-per-page']);
            let total = parseInt(data.headers['x-pagination-total-count']);
            let current = parseInt(data.headers['x-pagination-current-page']);
            setTimeout(() => {
              commit('setPageInfo', { size, total, current });
              commit('setPhotoList', data.data);
            }, 100);
            resolve(data.data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.state  [description]
     * @param  {[type]} options.commit [description]
     * @param  {[type]} data           [description]
     * @return {[type]}                [description]
     */
    DeletePhotoList({ state, commit }, value) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .gallery('delete', value)
          .then(data => {
            VM.$message({
              type: 'success',
              message: ''
            });
            resolve(data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.commit   [description]
     * @param  {[type]} options.userName [description]
     * @param  {[type]} options.password [description]
     * @return {[type]}                  [description]
     */
    MovePhotoList({ state, commit, dispatch }, { list, group_id }) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .gallery('put', { behavior: 'setgroup', id: list }, { group_id: group_id })
          .then(data => {
            VM.$message({
              type: 'success',
              message: ''
            });
            resolve(data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    },
    /**
     * 
     * @param  {[type]} options.state  [description]
     * @param  {[type]} options.commit [description]
     * @return {[type]}                [description]
     */
    UpdatePhotoItem({ state, commit }, value) {
      let VM = this._vm;
      let $heshop = this._vm.$heshop;
      return new Promise((resolve, reject) => {
        $heshop
          .gallery('put', parseInt(value.id), { title: value.name })
          .then(data => {
            VM.$message({
              type: 'success',
              message: ''
            });
            resolve(data);
          })
          .catch(error => {
            VM.$message({
              type: 'error',
              message: ''
            });
            reject(error);
          });
      });
    }
  }
};
