<template>
    <div class="le-matter">
        <div class="he-title">
            <el-breadcrumb separator-class="el-icon-arrow-right" class="he-link-text">
                <el-breadcrumb-item class="he-link">
                    <he-link href="channel/index">
                        
                    </he-link>
                </el-breadcrumb-item>
                <el-breadcrumb-item class="he-link">
                    <he-link href="channel/bind" :query="{platform: platform}">
                        {{title}}
                    </he-link>
                </el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <el-card>
            <div class="flex flex-direction align-center">
                <div class="le-steps">
                    <el-steps :active="step" align-center>
                        <el-step title=""></el-step>
                        <el-step title=""></el-step>
                    </el-steps>
                </div>
                <el-form v-show="step === 1" label-width="100px" class="le-form" ref="form" :model="form" :rules="rules">
                    <el-form-item label="" prop="name">
                        <el-input v-model="form.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="type">
                        <el-select v-model="form.type" placeholder="">
                            <el-option v-for="item in typeList" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="AppID" prop="appId">
                        <el-input v-model="form.AppID" placeholder="AppID"></el-input>
                    </el-form-item>
                    <el-form-item label="AppSecret" prop="appSecret">
                        <el-input v-model="form.AppSecret" placeholder="AppSecret"></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="file">
                        <el-input v-model="form.file_name">
                            <template slot="append">
                                <el-upload action="" ref="upload" :on-change="handleChange" :http-request="upload" :limit="1" :auto-upload="false" :show-file-list="false"></el-upload>
                            </template>
                        </el-input>
                        <el-button class="le-view-guidelines" module="channel" title="" type="text" width="720" :hide_close="true" height="676" v-popup.uploadGuide sure_btn=""></el-button>
                    </el-form-item>
                </el-form>
                <div class="le-deploy" v-show="step === 2">
                    <div class="le-deploy-1"></div>
                    <div class="le-deploy-2">
                        <span class="le-deploy-1"></span>
                        <el-button class="le-view-guidelines" module="channel" title="" type="text" width="720" :hide_close="true" v-popup.deployGuide sure_btn=""></el-button>
                    </div>
                    <div class="le-deploy__item">
                        <span class="le-item__label">(URL)</span>
                        <span class="le-item__value" id="url">{{form.url}}</span>
                        <el-button type="text" class="le-view-guidelines" @click="copy('url')"></el-button>
                    </div>
                    <div class="le-deploy__item">
                        <span class="le-item__label">(Token)</span>
                        <span class="le-item__value" id="token">{{form.token}}</span>
                        <el-button type="text" class="le-view-guidelines" @click="copy('token')"></el-button>
                    </div>
                    <div class="le-deploy__item flex align-center">
                        <span class="le-item__label"><br />(EncodingAESKey)</span>
                        <span class="le-item__value" id="key">{{form.encodingAesKey}}</span>
                        <el-button type="text" class="le-view-guidelines" @click="copy('key')"></el-button>
                    </div>
                </div>
            </div>
        </el-card>
        <div class="le-cardpin">
            <el-button></el-button>
            <el-button type="primary" v-if="step === 1" @click="setStep(2)"></el-button>
            <template v-if="step === 2">
                <el-button @click="setStep(1)"></el-button>
                <el-button type="primary" @click="save"></el-button>
            </template>
        </div>
    </div>
</template>
<script>
export default {
    name: "manualBinding",
    data() {
        return {
            title: '',
            platform: '',
            form: {
                type: '',
                name: '',
                AppID: '',
                AppSecret: '',
                file_name:'',
                token: '',
                encodingAesKey: '',
                url: ''
            },
            rules: {
                name: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                type: [
                    { required: true, message: '', trigger: 'change' }
                ],
                appId: [
                    { required: true, message: 'AppID', trigger: 'blur' },
                ],
                appSecret: [
                    { required: true, message: 'AppSecret', trigger: 'blur' },
                ]
            },
            typeList: [{
                    label: '',
                    value: 10
                },
                {
                    label: '',
                    value: 11
                },
                {
                    label: '',
                    value: 20
                },
                {
                    label: '//',
                    value: 21
                }
            ],
            step: 1
        }
    },
    mounted() {
        let platform = this.$getQueryVariable('platform');
        this.platform = platform;
        switch (platform) {
            case "applets":
                this.title = "";
                break;
            case "wechat":
                this.title = "";
                break;
        }

        this.getConfig();
    },
    methods: {
        getConfig: function() {
            this.$heshop.appconfig('get').then(res => {
                this.form = res.apply.wechat
                this.form.file_name = ''
            }).catch(err => {
                this.$message.error(err.data.message);
            })
        },
        setStep: function(num) {
            this.step = num;
        },
        save: function() {
            let data = {
                type: this.form.type,
                name: this.form.name,
                AppID: this.form.AppID,
                AppSecret: this.form.AppSecret,
                token: this.form.token,
                encodingAesKey: this.form.encodingAesKey
            }
            this.$heshop.appconfig('post', {
                    behavior: 'save',
                    key: 'apply_wechat'
                },
                data
            ).then(res => {
                this.$notify({
                    title: '',
                    message: '',
                    type: 'success'
                });
                this.$refs.upload.submit();
            }).catch(err => {
                console.error(err)
                this.$message.error(err.data.message);
            })
        },
        handleChange: function(e) {
            this.form.file_name = e.name;
            this.$forceUpdate();
        },
        upload(param){
            var formData = new FormData();
            formData.append('file', param.file);
            this.$heshop.file(formData, { behavior: 'upload' }).then(() => {
            }).catch(error => {
                console.error("", error)
            })
        },
        copy: function(id, attr = 'innerText') {
            let target = null;

            if (attr) {
                target = document.createElement('div');
                target.id = 'tempTarget';
                target.style.opacity = '0';
                if (id) {
                    let curNode = document.querySelector('#' + id);
                    target.innerText = curNode[attr];
                } else {
                    target.innerText = attr;
                }
                document.body.appendChild(target);
            } else {
                target = document.querySelector('#' + id);
            }

            try {
                let range = document.createRange();
                range.selectNode(target);
                window.getSelection().removeAllRanges();
                window.getSelection().addRange(range);
                document.execCommand('copy');
                window.getSelection().removeAllRanges();
            } catch (e) {
                console.error('')
            }

            if (attr) {
                // remove temp target
                target.parentElement.removeChild(target);
            }
        }
    }
};
</script>
<style scoped>
.flex {
    display: flex;
}

.flex-direction {
    flex-direction: column;
}

.le-steps {
    width: 60%;
}

.align-center {
    align-items: center;
}

.he-title {
    margin-bottom: 24px;
}

.le-form {
    margin-top: 50px;
}

.le-view-guidelines {
    font-size: 14px;
    font-family: PingFang SC;
    font-weight: 500;
    color: #623ceb;
    padding: 0;
    margin-left: 16px;
}

.le-deploy {
    width: 40%;
    margin-top: 50px;
}

.le-deploy__item {
    margin-bottom: 20px;
}

.le-deploy-1 {
    font-size: 16px;
    font-family: PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 44px;
    opacity: 0.85;
}

.le-deploy-2 {
    margin-bottom: 35px;
}

.le-item__label {
    text-align: right;
    width: 123px;
    font-size: 14px;
    font-family: PingFang SC;
    font-weight: 500;
    color: #333333;
    line-height: 20px;
    opacity: 0.65;
    margin-right: 15px;
    display: inline-block;
}

.le-item__value {
    font-size: 14px;
    font-family: Microsoft YaHei;
    font-weight: 400;
    color: #000000;
    opacity: 0.85;
}
</style>
