/**
 * 
 * @param  {[type]} argument [description]
 * @return {[type]}          [description]
 */
function inBrowser() {
  return typeof window !== 'undefined';
}

/**
 * 
 * @param  {[type]}  value [description]
 * @param  {[type]}  array [description]
 * @return {Boolean}       [description]
 */
function inArray(value, array) {
  if (array.indexOf(value) == -1) {
    return false;
  }
  return true;
}

/**
 * 
 * @param {[type]} key   [description]
 * @param {[type]} value [description]
 */
function setCache(key, value) {
  localStorage.setItem(key, value);
}

/**
 * 
 * @param  {[type]} key [description]
 * @return {[type]}     [description]
 */
function getCache(key) {
  return localStorage.getItem(key);
}

/**
 * Token
 * @param {[type]} value [description]
 */
function setToken(value) {
  localStorage.setItem('_hesop_token_', value);
}

/**
 * Token
 * @param  {[type]} argument [description]
 * @return {[type]}          [description]
 */
function getToken() {
  return localStorage.getItem('_hesop_token_');
}

/**
 * Token
 * @param  {[type]} argument [description]
 * @return {[type]}          [description]
 */
function removeToken() {
  localStorage.removeItem('_hesop_token_');
}

/**
 *,
 *
 *
 * @param  {[type]} obj   [description]
 * @param  {Array}  cache [description]
 * @return {[type]}       [description]
 */
function copyData(obj, cache = []) {
  // obj
  if (obj === null || typeof obj !== 'object') {
    return obj;
  }

  // obj
  const hit = find(cache, c => c.original === obj);
  if (hit) {
    return hit.copy;
  }

  const copy = Array.isArray(obj) ? [] : {};
  //
  //copyData
  cache.push({
    original: obj,
    copy
  });

  Object.keys(obj).forEach(key => {
    copy[key] = copyData(obj[key], cache);
  });

  return copy;
}

/**
 * 
 * @param  {[type]}  data     [description]
 * @param  {Boolean} is_error [description]
 * @return {[type]}           [description]
 */
function getShift(data, is_error = false) {
  if (data) {
    return Object.keys(data).shift();
  } else {
    return false;
  }
}

/**
 * 
 * @param {any} element  dom
 * @param {any} event    
 * @param {any} listener 
 */
function addEvent(element, event, listener, bubble = false) {
  if (element.addEventListener) {
    element.addEventListener(event, listener, bubble);
  } else if (element.attachEvent) {
    element.attachEvent('on' + event, listener);
  } else {
    element['on' + event] = listener;
  }
}

/**
 * 
 * @param  {[type]} $obj [description]
 * @return {[type]}      [description]
 */
function Empty($obj) {
  // /  EXundefined   null  ''    0 ,'0','  '  
  if (!$obj && $obj !== 0 && $obj !== '') return true;
  if (typeof $obj === 'string') {
    //  ''
    $obj = $obj.replace(/\s*/g, '');
    if ($obj === '') return true;
  }
  return (
    (Array.isArray($obj) && $obj.length === 0) ||
    (Object.prototype.isPrototypeOf($obj) && Object.keys($obj).length === 0)
  );
}

/**
 * 
 * @param  {[type]} component [description]
 * @param  {Object} props     [description]
 * @param  {Object} data      [description]
 * @param  {[type]} parent    [description]
 * @param  {[type]} el        [description]
 * @return {[type]}           [description]
 */
function getInstance(Component, props = {}, options = {}) {
  let instance = new Component({
    propsData: props,
    parent: options.parent || null,
    data() {
      return options.data || {};
    },
    el: options.el || document.createElement('div')
  });
  return instance;
}

function indexOf(arr, val) {
  for (let i = 0; i < arr.length; i++) {
    if (arr[i] == val) {
      return i;
    }
  }
  return -1;
}

function arrayRemove(arr, val) {
  let index = indexOf(arr, val);
  if (index > -1) {
    arr.splice(index, 1);
  }
}

/**
 * 
 * @param  {[type]} val [description]
 * @return {[type]}     [description]
 */
function getQueryVariable(variable) {
  var query = window.location.search.substring(1);
  var vars = query.split('&');
  for (var i = 0; i < vars.length; i++) {
    var pair = vars[i].split('=');
    if (pair[0] == variable) {
      return pair[1];
    }
  }
  return false;
}

/**
 * 
 * @param  {[type]} str [description]
 * @return {[type]}     [description]
 */
function filter(str) {
  str += ''; // 
  str = str.replace(/%/g, '%25');
  str = str.replace(/\+/g, '%2B');
  str = str.replace(/ /g, '%20');
  str = str.replace(/\//g, '%2F');
  str = str.replace(/\?/g, '%3F');
  str = str.replace(/&/g, '%26');
  str = str.replace(/\=/g, '%3D');
  str = str.replace(/#/g, '%23');
  return str;
}

/**
 * URL
 * @param  {[type]} paramObj [description]
 * @return {[type]}          [description]
 */
function formateObjToParamStr(paramObj) {
  const sdata = [];
  for (let attr in paramObj) {
    sdata.push(`${attr}=${filter(paramObj[attr])}`);
  }
  return sdata.join('&');
}

/**
 * 
 * @param  {[type]} obj [description]
 * @return {[type]}          [description]
 */
function deepClone(obj) {
  if ([null, undefined, NaN, false].includes(obj)) return obj;
  if (typeof obj !== 'object' && typeof obj !== 'function') {
    return obj;
  }
  let o = Object.prototype.toString.call(obj) === '[object Array]' ? [] : {};
  for (let i in obj) {
    if (obj.hasOwnProperty(i)) {
      o[i] = typeof obj[i] === 'object' ? deepClone(obj[i]) : obj[i];
    }
  }
  return o;
}

//HTML
function HTMLDecode(text) {
  var temp = document.createElement('div');
  temp.innerHTML = text;
  var output = temp.innerText || temp.textContent;
  temp = null;
  return output;
}

//HTML
function HTMLEncode(html) {
  var temp = document.createElement('div');
  temp.textContent != null ? (temp.textContent = html) : (temp.innerText = html);
  var output = temp.innerHTML;
  temp = null;
  return output;
}

export default {
  inBrowser,
  inArray,
  setCache,
  getCache,
  setToken,
  getToken,
  removeToken,
  copyData,
  getShift,
  addEvent,
  Empty,
  getInstance,
  arrayRemove,
  getQueryVariable,
  filter,
  formateObjToParamStr,
  deepClone,
  HTMLDecode,
  HTMLEncode
};
