import * as extend from './extend';
import cache from './cache';

class api {
  /**
   * API
   * @param  {[type]} data   [description]
   * @param  {[type]} server [description]
   * @return {[type]}        [description]
   */
  constructor(data, cloud) {
    //cloud
    this.cloud = cloud;
    //
    return new Promise((reslove, reject) => {
      //-
      this.post(data)
        .then(value => {
          return this.afterEach(value)
            .then(reverse => {
              reslove(reverse);
            })
            .catch(error => {
              reject(error);
            });
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  /**
   * 
   * @param  {[type]} data [description]
   * @return {[type]}      [description]
   */
  beforeEach(value) {
    return new Promise((resolve, reject) => {
      try {
        this.basics.beforeEach(value, resolve, this.cloud);
      } catch (error) {
        reject(error);
      }
    });
  }

  /**
   * 
   * @param  {[type]} data [description]
   * @return {[type]}      [description]
   */
  afterEach(value) {
    return new Promise((resolve, reject) => {
      try {
        this.basics.afterEach(value, resolve);
      } catch (error) {
        reject(error);
      }
    });
  }

  /**
   * 
   * @return {[type]} [description]
   */
  post(data) {
    //POST
    return new Promise((reslove, reject) => {
      //Key
      let keys = Object.keys(data);
      //
      let action = keys[0] || 'defaulter';
      //
      this.basics = extend[action] || extend['defaulter'];
      //Server
      let server = this.cloud.server;
      /**
       * 
       * @param  {Function} data).then(data [description]
       * @return {[type]}                   [description]
       */

      this.beforeEach(data)
        .then(value => {
          if (value) {
            let options = {
              url: value.url,
              method: value.method,
              responseType: value.responseType || '',
              headers: value.headers,
              data: value.data,
              adapter: value.responseType
                ? ''
                : cache({
                    time: 1000
                  })
            };
            this.cloud
              .server(options)
              .then(response => {
                reslove(response);
              })
              .catch(error => {
                reject(error);
              });
          } else {
            reslove(true);
          }
        })
        .catch(error => {
          reject(error);
        });
    });
  }
}

export default api;
